import React from "react";
import { CCard } from "@coreui/react";
import BootstrapTable from "react-bootstrap-table-next";
import "./bootstrapTable-style.scss";
import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";
import axios from "axios";

//cell editable
//doc : https://react-bootstrap-table.github.io/react-bootstrap-table2/storybook/index.html?selectedKind=Cell%20Editing&selectedStory=Click%20to%20Edit&full=0&addons=1&stories=1&panelRight=0&addonPanel=storybook%2Factions%2Factions-panel
import cellEditFactory from "react-bootstrap-table2-editor";

class TableEditable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      validateStatus: false,
      validateResult: "Please Wait...",
      globalData: this.props.globalData,
      rowCount: 0,
      keyy: 0,
      triggerRender: 0
    };  
    this.validatePOSKUStockTransfer = this.validatePOSKUStockTransfer.bind(this);
    this.validateSKUNumber = this.validateSKUNumber.bind(this);
    
  } 
  changeOutboundReason = (row) => {
    setTimeout(() => {
      let elem = document.getElementById(`row${row}-outboundReason`);
      elem.classList.add("red", "tooltip-custom");
      elem.innerHTML = `Outbound Reason Can't be empty<span class="tooltiptext">Outbound Reason Can't be empty if Outbound type = 'Lainnya'</span>`;
    }, 300);
  }

  changeWarehouseUuid = (
    value,
    column,
    columnName,
    row,
    warehouseCodeList,
    warehouseUuidList,
    warehouseNameList
  ) => {
    const { setCsvDataTmp, globalData } = this.props;
    let tmpdata = globalData;
    var index = tmpdata.findIndex((obj) => obj.id == row);
    let i = warehouseCodeList.indexOf(value);
    tmpdata[index][column] = warehouseUuidList[i];
    tmpdata[index][columnName] = warehouseNameList[i];

    console.clear() 
    setTimeout(async () => {
      setCsvDataTmp(tmpdata);
    }, 400);
  };

  validatePOSKU = async (id, row, idColumn) => {
    const { setCsvDataTmp, globalData, setIsValidate, transaction } = this.props;
    if(transaction=="additional_inbound"){
      return;
    }

    let tmpdata = globalData;
    try {
      setIsValidate(false);
      const url = "/stocktake/rowValidationUpload";
      let elem = null;
      let elemspan = null;
      setTimeout(async () => {
        elem = document.getElementById(`${idColumn}`);
        elemspan = document.getElementById(`${idColumn}span`);
        if (elem === null || elem === undefined) {
          return 0;
        }
        elemspan.innerHTML = "Validating";
        elem.classList.add("red");
        await axios({
          method: "post",
          url: url,
          data: { data: [id] },
        }).then(function (response) {
          let data = response?.data?.data;
          var index = tmpdata.findIndex((obj) => obj.id == row);
          if (data[0] && data[0].isExist === true) {
            tmpdata[index].wmsPoSkuCode = data[0].wmsPoSkuCode;
            tmpdata[index].wmsPoSkuCodeStatus = true;
            tmpdata[index].wmsPoSkuCodeReason = "PO SKU Code Not Found";
            tmpdata[index].expiryDate = data[0].expiryDate;
            tmpdata[index].inboundDate = data[0].inboundDate;
            tmpdata[index].supplierName = data[0].supplierName;
            tmpdata[index].supplierUuid = data[0].supplierUuid;
            tmpdata[index].uom = data[0].uom;
            tmpdata[index].photos = data[0].photos;
            elem.classList.remove("red");
            elem.classList.add("hiddenTooltip");
          } else {
            tmpdata[index].wmsPoSkuCodeStatus = false;
            tmpdata[index].wmsPoSkuCodeReason = "PO SKU Code Not Found";
            tmpdata[index].expiryDate = "";
            tmpdata[index].inboundDate = "";
            tmpdata[index].supplierName = "";
            tmpdata[index].supplierUuid = "";
            elem.classList.add("red");
            elem.classList.remove("hiddenTooltip");
            elemspan.innerHTML = "PO SKU Code Not Found";
          }
          setCsvDataTmp(tmpdata);
        });
      }, 400);
    } catch (error) {
      return false;
    }
  };

  validatePOSKUStockTransfer = async (newValue, row, idColumn, key) => {
    const { setCsvDataTmp, globalData, setIsValidate, transaction } = this.props;  
    let tmpdata = globalData;
    
    var index = tmpdata.findIndex((obj) => obj.id == row.id); 
    // console.log(tmpdata)
    // console.log(row.id, row)
    // console.log(index)

    try {
      const url = "/stocktransfer/rowValidationUpload";
      let elem = null;
      let elemspan = null;
      let payloadPO = [{
        "id": row.id,
        "poSkuCode": key=="wmsPoSkuCode" ? newValue : (row.poSkuCode?row.poSkuCode:""),
        "skuNumber": key=="skuNumber"? newValue?.skuNumber: row.skuNumber,
        "skuUuid": key=="skuNumber"? newValue?.skuUuid:row.skuUuid,
        "warehouseSource": row.warehouseSource,
        "areaSource": row.areaSource,
        "weight": key=="qty"?newValue:row.qty
      }]; 

      setTimeout(async () => {
        //setvalidate false
        setIsValidate(false);

        //elemen column
        elem = document.getElementById(`${idColumn}`);
        elemspan = document.getElementById(`${idColumn}span`);

        if (elem === null || elem === undefined) {
          return 0;
        }
        elemspan.innerHTML = "Validating";
        // elem.classList.remove("hiddenTooltip");
        // elem.classList.add("red");  
        await axios({
          method: "post",
          url: url,
          data: { data: payloadPO },
        }).then(function (response) {
          let data = response?.data?.data;
          if (data[0]) {
            if(data[0].isExist === true && data[0].isValid === true){
              tmpdata[index]['rowStatus'] = true;
              tmpdata[index]['rowReason'] = "";
              elem.classList.remove("red");
              elem.classList.add("hiddenTooltip");
            }else{
              tmpdata[index]['rowStatus'] = false;
              tmpdata[index]['rowReason'] = data[0]?.errorMessage;
            }
            tmpdata[index]['wmsPoSkuCode'] = data[0]?.wmsPoSkuCode ;
            tmpdata[index]['wmsPoSkuCodeStatus'] = data[0]?.isExist && data[0]?.isValid;
            tmpdata[index]['wmsPoSkuCodeReason'] = data[0]?.errorMessage;
            tmpdata[index]['expiryDate'] = data[0]?.expiryDate;
            tmpdata[index]['inboundDate'] = data[0]?.inboundDate;
            tmpdata[index]['supplierName'] = data[0]?.supplierName;
            tmpdata[index]['supplierUuid'] = data[0]?.supplierUuid;
            tmpdata[index]['uom'] = data[0]?.uom;
            tmpdata[index]['photos'] = data[0]?.photos;
          } else {
            tmpdata[index]['wmsPoSkuCodeStatus'] = false;
            tmpdata[index]['wmsPoSkuCodeReason'] = response?.data?.message;
            tmpdata[index]['expiryDate'] = "";
            tmpdata[index]['inboundDate'] = "";
            tmpdata[index]['supplierName'] = "";
            tmpdata[index]['supplierUuid'] = "";
            tmpdata[index]['rowStatus'] = false;
            tmpdata[index]['rowReason'] = response?.data?.message;
            elem.classList.add("red");
            elem.classList.remove("hiddenTooltip");
            elemspan.innerHTML = response?.data?.message;
          } 

          if(key=="skuNumber"){ 
            tmpdata[index]["skuNumber"] = newValue.skuNumber;
            tmpdata[index]["skuNumberReason"] = newValue.skuDescription;
            tmpdata[index]["skuNumberStatus"] = true;
            tmpdata[index]["skuUuid"] = newValue.skuUuid;
          }
          setCsvDataTmp(tmpdata); 
        }); 
        let tmpKey = this.state.keyy + 1; 
        this.setState({keyy: tmpKey}); 
      }, 500);
    } catch (error) {
      return false;
    }
  };

  validateOutbountReason = (value, row, idColumn) => {
    const { globalData } = this.props;
    let tmp_data = globalData;
    var index = tmp_data.findIndex((obj) => obj.id == row);
    let datax = tmp_data[index];
    if (value == "" && datax["outboundType"].toUpperCase() == "LAINNYA") {
      this.changeOutboundReason(row)
    }
  };

  validateOutbountType = (value, row, idColumn) => {
    const { globalData, setIsValidate } = this.props;
    let tmp_data = globalData;
    setIsValidate(false);
    if (value.toUpperCase() !== "LAINNYA") {
      setIsValidate(true);
      return 0;
    }

    var index = tmp_data.findIndex((obj) => obj.id == row);
    let datax = tmp_data[index];
    if (datax["outboundReason"] == "") {
      this.changeOutboundReason(row);
    }
  };

  validateSKUNumber = async (id, row, idColumn, key, rowData = null) => {
    const { globalData, setCsvDataTmp, setIsValidate,transaction } = this.props;
    let tmp_data = globalData;
    try {
      setIsValidate(false);
      const url = "/oss/skuByNumbers";
      let elem = null;
      let elemspan = null;
      
      // for validating stock transfer
      let stats = false;
      let newSkuData = {} 
      setTimeout(async () => {
        elem = document.getElementById(`${idColumn}`);
        elemspan = document.getElementById(`${idColumn}span`);
        if (elem === null || elem === undefined) {
          return 0;
        }
        elemspan.innerHTML = "Validating";
        await axios({
          method: "post",
          url: url,
          data: { skuList: [id] },
        }).then(function (response) {
          let data = response?.data?.data;
          let isSuccess = response.data.isSuccess;
          var index = tmp_data.findIndex((obj) => obj.id == row);
          if (isSuccess === true && data.length > 0) {
            stats = true;
            tmp_data[index][key] = data[0].skuNumber;
            tmp_data[index][key + "Reason"] = data[0].skuDescription;
            tmp_data[index][key + "Status"] = true;
            tmp_data[index][key + "Uuid"] = data[0].id;
            newSkuData = {
              skuUuid: data[0].id,
              skuNumber: data[0].skuNumber,
              skuDescription: data[0].skuDescription,
              photo: data[0].photo
            }

            //custom
            if (key == "skuNumber") {
              tmp_data[index].skuUuid = data[0].id;
              tmp_data[index].photo = data[0].imageMd;
            }

            if (key == "newGrade") {
              tmp_data[index].newGradeUom = data[0].uom;
            }
            
            if(transaction=="additional_inbound"){
              tmp_data[index].uom = data[0].uom;
            }

            
            if(transaction!=="stockTransfer"){
              elem.classList.remove("red");
              elem.classList.add("hiddenTooltip");
            }

          } else {
            stats = false;
            tmp_data[index][key] = id;
            tmp_data[index][key + "Reason"] = "SKU Number Not Found";
            tmp_data[index][key + "Status"] = false;
            tmp_data[index][key + "Uuid"] = null;
            elem.classList.add("red");
            elem.classList.remove("hiddenTooltip");
            elemspan.innerHTML = "SKU Number Not Found";

            if (key == "skuNumber") {
              tmp_data[index].skuUuid = "";
              tmp_data[index].photo = "";
            }
            if (key == "newGrade") {
              tmp_data[index].newGradeUom = null;
            }
            if(transaction=="additional_inbound"){
              tmp_data[index].uom = "";
            }
          } 
        });

        if(transaction=="stockTransfer" && stats === true){ 
          this.validatePOSKUStockTransfer(newSkuData, rowData, idColumn, 'skuNumber')
        }else{
          setCsvDataTmp(tmp_data);
        }
      }, 400);
    } catch (error) {
      return false;
    }
  };

  render() {
    const {
      columns,
      hiddenRows,
      tableClass,
      warehouseArea,
      outboundType,
      setCheckingCsv, 
      editable = true,
      warehousesData = [],
      transaction,
      globalData
    } = this.props;
    let { validateStatus,  keyy } = this.state;
    
    //wh
    let warehouseCodeList = [];
    let warehouseUuidList = [];
    let warehouseNameList = [];
    if (warehousesData) {
      let tmp = warehousesData?.data?.map((d) => {
        warehouseCodeList.push(d.code);
        warehouseUuidList.push(d.id);
        warehouseNameList.push(d.name);
      });
    }

    //wh area
    let wh_area_label =
      warehouseArea.map((data) => data.label.toUpperCase()) || [];
    let wh_area_value = warehouseArea.map((data) => data.value) || [];
    let wh_outboundtype_label = !outboundType
      ? []
      : outboundType.map((data) => data.label.toUpperCase());
    let wh_outboundtype_value = !outboundType
      ? []
      : outboundType.map((data) => data.value);
    const newFormat = columns.map((data, index) => {
      data.validator = (newValue, row, column, done) => {
        let key = data.dataField;
        let keyStatus = key + "Status";
        let keyReason = key + "Reason";
        let stat = true;
        let reason = null;
        let idColumn = "row" + row["id"] + "-" + key;

        let stockTransferColumnCheck = ['wmsPoSkuCode', 'qty']
        if (stockTransferColumnCheck.includes(key) && transaction=="stockTransfer") {
          this.validatePOSKUStockTransfer(newValue, row, idColumn, key);
          stat = true;
        }else if(key == "wmsPoSkuCode" ){
          this.validatePOSKU(newValue, row.id, idColumn, key);
          stat = true; 
        }

        if (key == "skuNumber") {
          this.validateSKUNumber(newValue, row.id, idColumn, key, row);
          stat = true;
        }

        let arr_grade = ["WASTE", "SEMI WASTE"];
        if (key == "newGrade" && this.props.transaction == "waste") {
          if (!arr_grade.includes(newValue.toUpperCase())) {
            reason = "Waste Type not found";
            checkType = false;
            stat = false;
          } else {
            row["newGradeUuid"] = newValue;
            row["newGradeUom"] = null;
          }
        } else if (key == "newGrade") {
          if (!arr_grade.includes(newValue.toUpperCase())) {
            //if not waste or semi waste
            if (parseInt(newValue)) {
              //if number
              stat = true;
              this.validateSKUNumber(newValue, row.id, idColumn, key);
            } else if (newValue == "") {
              row["newGradeUuid"] = null;
              row["newGradeUom"] = null;
              stat = true;
            } else {
              //if not number => undefined
              reason = "Grade Undefined";
              checkType = false;
              stat = false;
            }
          } else {
            row["newGradeUuid"] = newValue;
            row["newGradeUom"] = null;
          }
        }

        if (key == "outboundType" && wh_outboundtype_value.length > 0) {
          if (!wh_outboundtype_label.includes(newValue.toUpperCase())) {
            stat = false;
            reason = `Outbound Type Not Found`;
          } else {
            let i = wh_outboundtype_label.indexOf(newValue.toUpperCase());
            row["outboundTypeCode"] = wh_outboundtype_value[i];
            stat = true;
            this.validateOutbountType(newValue, row.id, idColumn);
          }
        }

        if (key == "outboundReason") {
          this.validateOutbountReason(newValue, row.id, idColumn);
        }

        if (key == "warehouse") {
          if (!warehouseCodeList.includes(newValue)) {
            stat = false;
            reason = `Warehouse Not Found`;
          } else {
            stat = true;
            this.changeWarehouseUuid(
              newValue,
              "warehouseUuid",
              "warehouseName",
              row.id,
              warehouseCodeList,
              warehouseUuidList,
              warehouseNameList
            );
          }
        }
        if (key == "warehouseDestination") {
          if (!warehouseCodeList.includes(newValue)) {
            stat = false;
            reason = `Warehouse Not Found`;
          } else {
            stat = true;
            this.changeWarehouseUuid(
              newValue,
              "warehouseDestinationUuid",
              "warehouseDestinationName",
              row.id,
              warehouseCodeList,
              warehouseUuidList,
              warehouseNameList
            );
          } 
        }

        if (key == "areaName" || key == "areaNameDestination") {
          if (!wh_area_label.includes(newValue.toUpperCase())) {
            stat = false;
            reason = `Warehouse Area Not Found`;
          } else {
            let i = wh_area_label.indexOf(newValue.toUpperCase());
            if (key == "areaNameDestination") {
              row["areaDestination"] = wh_area_value[i];
            } else {
              row["areaSource"] = wh_area_value[i];
            }
            stat = true;
          }
        }

        if (key == "newPack") {
          let checkTypenewPack = isNaN(newValue) ? false : true;
          if (!checkTypenewPack) {
            stat = false;
            reason = `Pack Should be a number`;
          } else {
            stat = true;
          }
        } 
        

        let checkType = null;
        if (data.type == "number") {
          checkType = isNaN(newValue) ? false : true;
        } else {
          checkType = true;
        }
        if (checkType === false) {
          reason = `${data.text} Should be a ${data.type}`;
        } 

        
        if (key == "wmsPoSkuCode" && transaction == "stockTransfer") {
          if(isNaN(newValue)){
            checkType = false;
            reason = `${data.text} Should be a number`;
            stat = false;
          } 
        } 

        //some field can null
        let canNull = [
          "grade",
          "outboundReason",
          "ripenessLevel",
          "newRipeness",
          "reason",
          "expiryDate"
        ]; 

        if (canNull.includes(key) && newValue === "") {
          reason = "";
          checkType = true;
          stat = true;
        } else if (key == "grade") {
          if (!arr_grade.includes(newValue.toUpperCase())) {
            reason = "Waste Type Not Found";
            checkType = false;
            stat = false;
          }
        }

        //check ripenesslevel
        if (key == "ripenessLevel" || key == "newRipeness") {
          let allowedRipenessLevel = [0, 1, 2, 3];
          if (
            allowedRipenessLevel.includes(parseInt(newValue)) ||
            newValue == ""
          ) {
            checkType = true;
            stat = true;
            reason = "";
            if (newValue == "") {
              newValue = "";
            }
          } else {
            checkType = false;
            stat = false;
            reason = "Undefined Ripeness Level";
          }
        }

        //check date
        if (data.type == "date_indo") {
          //checking date
          let regex_date = /(0[1-9]|[12][0-9]|3[01])[-.](0[1-9]|1[012])[-.](19|20)\d\d/;
          if (newValue.match(regex_date)) {
            checkType = true;
            stat = true;
          } else if (newValue == "") {
            checkType = true;
            stat = true;
          } else {
            reason = `Date format must be 'dd-mm-yyyy'`;
            checkType = false;
            stat = false;
          }
        }

        //set notif
        row[keyReason] = reason;
        if (stat && checkType) {
          let specialKey = ["wmsPoSkuCode", "skuNumber"];
          if (!specialKey.includes(key)) {
            //some key have their own checking, so if not in array do checking error
            row[keyStatus] = true;
            setCheckingCsv();
          }
        } 
         
        return {
          valid: stat && checkType ? true : false,
          message: reason,
        };
      };

      data.formatter = (cell, row, index) => {
        let key = data.dataField;
        let keyStatus = key + "Status";
        let keyReason = key + "Reason";
        let stat = true;
        let idColumn = "row" + row["id"] + "-" + key;
        let txt = row[keyReason];
        
        // if status column = false
        // that mean there's error or not valid data in some column
        if (row[keyStatus] === false) {
          validateStatus = false;
          stat = false;
          return (
            <div id={idColumn} className="tooltip-custom red">
              {cell}
              <span id={idColumn + "span"} className="tooltiptext">
                {txt}
              </span>
            </div>
          );
        }

        // if global row status = false
        // that mean there's wmsposkucode validation error, and makes 1 line become red colour
        // for now is only for stock transfer  
        if ( transaction=="stockTransfer" &&  row['rowStatus'] === false) { 
          validateStatus = false;
          stat = false;
          return (
            <div id={idColumn} className="tooltip-custom red">
              {cell}
              <span id={idColumn + "span"} className="tooltiptext">
                {row['rowReason']}
              </span>
            </div>
          );
        }

        
        // if status column = true
        // No problem 
        return (
          <div id={idColumn} className="tooltip-custom hiddenTooltip">
            {cell}
            <span id={idColumn + "span"} className="tooltiptext">
              {txt}
            </span>
          </div>
        );
      };
      return data;
    });

    const sortOption = {
      // No need to configure sortFunc per column
      sortFunc: this.sortFunc,
      // No need to configure sortCaret per column
      sortCaret: (order, column) => {
        if (!order) return <i className="iconU-tableSortingNull ml-2"></i>;
        else if (order === "asc")
          return <i className="iconU-tableSortingTop ml-2"></i>;
        else if (order === "desc")
          return <i className="iconU-tableSortingBottom ml-2"></i>;
        return null;
      },
    };
    console.clear();  
    console.log(globalData)
    return (
      <CCard className="table-responsive">
        <BootstrapTable
          keyField="id"
          data={globalData}
          columns={newFormat}
          sort={sortOption}
          classes={tableClass || null}
          cellEdit={
            editable === true
              ? cellEditFactory({
                  mode: "click",
                  blurToSave: true,
                  nonEditableRows: () => this.props.dataDoubleRow || [],
                })
              : null
          }
          bordered={false}
          hiddenRows={hiddenRows}
          key={keyy}
        />
      </CCard>
    );
  }
}

class TableResult extends React.Component {
  render() {
    const { columns, tableClass, data } = this.props;

    const newFormat = columns.map((data, index) => {
      data.formatter = (cell, row, index) => {
        let key = data.dataField;
        if (key == "uploadStatus" && row["uploadStatus"] === "Failed") {
          return (
            <div className="tooltip-custom red">
              {cell}
              <span className="tooltiptext">{row["uploadStatusReason"]}</span>
            </div>
          );
        } else if (key == "uploadStatus" && row["uploadStatus"] === "Success") {
          return (
            <div className="green">
              {cell}
              <span></span>
            </div>
          );
        }

        return (
          <div>
            {cell}
            <span></span>
          </div>
        );
      };
      return data;
    });

    const sortOption = {
      // No need to configure sortFunc per column
      sortFunc: this.sortFunc,
      // No need to configure sortCaret per column
      sortCaret: (order, column) => {
        if (!order) return <i className="iconU-tableSortingNull ml-2"></i>;
        else if (order === "asc")
          return <i className="iconU-tableSortingTop ml-2"></i>;
        else if (order === "desc")
          return <i className="iconU-tableSortingBottom ml-2"></i>;
        return null;
      },
    };

    return (
      <CCard className="table-responsive">
        <BootstrapTable
          keyField="id"
          data={data}
          columns={newFormat}
          sort={sortOption}
          classes={tableClass || null}
          bordered={false}
        />
      </CCard>
    );
  }
}

export { TableResult, TableEditable };
