import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import { withRouter } from "react-router";
import { TableResult } from "Component/BootstrapTable";
import "../index.scss";

const Result = (props) => {

  //selector
  const selectorCsvFileName = useSelector((state) => state.csvFileName);
  const selectorCsvDataResult = useSelector((state) => state.csvDataResult);
  const selectorCsvFieldResult = useSelector((state) => state.csvFieldResult);

  console.log('selectorCsvDataResult', selectorCsvDataResult)
  console.log('csvFieldResult', selectorCsvFieldResult)


  useEffect(() => { }, [selectorCsvDataResult]);


  if (selectorCsvDataResult === undefined || selectorCsvDataResult === null) {
    props.history.push("/inventory-entry/additional-outbound/CSV-upload");
    return 0;
  }

  return (
    <div className="w-100 d-flex flex-column stock-take-summary">

      <TableResult
        columns={selectorCsvFieldResult}
        tableClass="reviewTable"
        data={selectorCsvDataResult}
      />

      <div className="row pt-3">
        <div className="col-4">
          <button
            type="button"
            className="btn px-3 mr-3 text no-shadow"
            style={{ backgroundColor: "#fff" }}
            onClick={() => props.history.push("/inventory-entry/additional-outbound/CSV-upload")}
          >
            Upload Another File
          </button>
        </div>

        <div className="col-4" style={{ lineHeight: "40px" }}>
          Review your imported file: {selectorCsvFileName} (
          {selectorCsvDataResult.length || 0} Lines)
        </div>
      </div>

    </div>
  );
};

export default withRouter(Result);
